.brands-icon-slider {
  width: 50%;
  margin: 200px auto 0px auto;
  padding-bottom: 500px;
  position: relative;
}

.brands-icon-slider .brand-icon-container {
  width: 100%;
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 20%;
}

.brands-icon-slider .brand-icon-container .brand-icon {
  width: 20%;
}

.brands-icon-slider .brand-icon-container .show {
  opacity: 100%;
  -webkit-animation-name: transitionShow;
          animation-name: transitionShow;
  -webkit-animation-duration: 0.15s;
          animation-duration: 0.15s;
}

.brands-icon-slider .brand-icon-container .unshow {
  opacity: 0%;
  -webkit-animation-name: transitionUnshow;
          animation-name: transitionUnshow;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}

@-webkit-keyframes transitionShow {
  from {
    opacity: 0%;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }
  to {
    opacity: 100%;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes transitionShow {
  from {
    opacity: 0%;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }
  to {
    opacity: 100%;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes transitionUnshow {
  from {
    opacity: 100%;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  to {
    opacity: 0%;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }
}

@keyframes transitionUnshow {
  from {
    opacity: 100%;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  to {
    opacity: 0%;
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
  }
}

